import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpParams} from '@angular/common/http';
import { Utilities } from "./utilities";
import { Observable, map } from 'rxjs';
import { Constants } from './constants';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

    public isAuthenticated = false;
    public authAccessToken = 'access_token';
    constructor( private router : Router,private httpClient:HttpClient) { 
    }
    
    public handleLoginError(error: any): string {
        // Xử lý lỗi ở đây và trả về một thông báo phù hợp
        // Ví dụ: 
        if (error.status === 401) {
            return 'Invalid username or password';
        } else {
            return 'An error occurred during login';
        }
    }
    public storeUserDetail(authToken,expTime) {
        Utilities.setCookie(this.authAccessToken, authToken, expTime);
    }
    public getUserToken(): string {
        let accessToken = Utilities.getCookie(this.authAccessToken);
        return accessToken;
    }
    public isLogin(): boolean {
        if (this.getUserToken()) {
            return true;
        } else return false;
    }
   

    logout(): void {
        Utilities.deleteCookie(this.authAccessToken);
        this.router.navigate(['/login']);
    }
}